import { Grid } from "@mui/material";
import { Box } from "@mui/material";

export const Hero = () => {
  const bgImage = "https://source.unsplash.com/random?wallpapers";

  return (
    <>
      <Box
        sx={{
          backgroundImage: { bgImage },
        }}
      >
        <Grid container>
          <Grid
            item
            sx={{
              color: "white",
              backgroundColor: "gray",
              padding: "48px",
              paddingRight: "0",
            }}
            xs={6}
          >
            <Box
              sx={{
                fontSize: "1.5rem",
              }}
            >
              <h1>Title of a longer featured blog post</h1>
            </Box>
            <Box
              sx={{
                fontSize: "1.5rem",
              }}
            >
              <p>
                Multiple lines of text that form the lede, informing new readers
                quickly and efficiently about what's most interesting in this
                post's contents.
              </p>
            </Box>
            <a href="#">Continued rreading...</a>
          </Grid>
          <Grid item xs={6}>
            <div></div>
          </Grid>
        </Grid>
      </Box>
    </>
  );
};
