import { Box, Button, Grid } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import { NavLink } from "../Nav/Nav";

export const Header = () => {
  return (
    <>
      <Grid
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          borderBottom: "1px solid #e0e0e0",
          padding: "0px 24px",
        }}
      >
        <Button>Subscribe</Button>
        <h2
          sx={{
            fontFamily: "Roboto",
            fontWeight: "400",
            fontSize: "1.5rem",
          }}
        >
          Blog
        </h2>
        <div>
          <Button variant="circular" width={40} height={40}>
            <SearchIcon />
          </Button>
          <Button variant="outlined">SIGN UP</Button>
        </div>
      </Grid>
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "0px 24px",
        }}
      >
        <NavLink>Technology</NavLink>
        <NavLink>Design</NavLink>
        <NavLink>Culture</NavLink>
        <NavLink>Business</NavLink>
        <NavLink>Politics</NavLink>
        <NavLink>Opinion</NavLink>
        <NavLink>Sciense</NavLink>
        <NavLink>Health</NavLink>
        <NavLink>Style</NavLink>
        <NavLink>Travel</NavLink>
      </Box>
    </>
  );
};
