import { Box, CardActions } from "@mui/material";
import { CardContent, CardMedia, Typography } from "@mui/material";
import Card from "@mui/material/Card";

export default function MediaCard() {
  return (
    <Box
      sx={{
        display: "flex",
        justifyContent: "space-between",
        padding: "32px 0px",
      }}
    >
      <Box
        sx={{
          display: "flex",
        }}
      >
        <Card sx={{ display: "flex", cursor: "pointer" }}>
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              Featured post
            </Typography>
            <Typography
              sx={{ width: "368px", mb: "16px" }}
              variant="body2"
              color="text.secondary"
            >
              Lizards are a widespread group of squamate reptiles, species,
              ranging across all continents except Antarctica
            </Typography>
            <CardActions sx={{ padding: "0" }}>
              <a href="#" size="small">
                Share
              </a>
            </CardActions>
          </CardContent>
          <CardMedia
            sx={{ height: "192px", width: "160px" }}
            image="data:image/jpeg;base64,"
            title="green iguana"
          />
        </Card>
      </Box>

      <Box
        sx={{
          display: "flex",
        }}
      >
        <Card sx={{ display: "flex", cursor: "pointer" }}>
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              Featured post
            </Typography>
            <Typography
              sx={{ width: "368px", mb: "16px" }}
              variant="body2"
              color="text.secondary"
            >
              Lizards are a widespread group of squamate reptiles, species,
              ranging across all continents except Antarctica
            </Typography>
            <CardActions sx={{ padding: "0" }}>
              <a href="#" size="small">
                Share
              </a>
            </CardActions>
          </CardContent>
          <CardMedia
            sx={{ height: "192px", width: "160px" }}
            image="data:image/jpeg;base64,"
            title="green iguana"
          />
        </Card>
      </Box>
    </Box>
  );
}
